  .iconBdown {
    position: absolute;
    top: 35%;
    left: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 30px;
    display: block;
    line-height: 50%;
    align-content: center;
  }
  
  .iconSdown {
    position: absolute;
    top: 35%;
    left: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 30px;
    display: block;
    line-height: 50%;
    align-content: center;
  }
  .iconBup {
    position: absolute;
    top: 65%;
    left: 50%;
    height: 50%;
    transform: translate(-50%, -50%);
