  .breakLength {
    font-family: "Open Sans", sans-serif;
    text-transform: lowercase;
    color: #585858;
  }
  .sessionLength {
    font-family: "Open Sans", sans-serif;
    text-transform: lowercase;
    color: #585858;
  }

  .timeLeft {
    font-family: "Open Sans", sans-serif;
    color: rgb(43, 41, 42);
  }
  
  h4 {
    font-size: 38px;
    border: 6px solid rgb(250, 179, 158);
    border-radius: 7px;
    width: 160px;
    height: 70px;
    margin: 20px auto 10px auto;
    text-align: center;
    align-self: flex-end;
  }
  
  .start {
    padding: 8px;
    font-size: 15px;
  }
  .reset {
    padding: 8px;
    font-size: 15px;
  }
  